import React, { Component, PropTypes } from 'react';
import '../style/stopwatch.css';
import { Column, Row } from 'simple-flexbox';

class Controls extends Component {

  // static proptTypes = {
  //   isRunning  : PropTypes.bool,
  //   start      : PropTypes.func.isRequired,
  //   stop       : PropTypes.func.isRequired,
  //   reset      : PropTypes.func.isRequired,
  //   addLapTime : PropTypes.func.isRequired 
  // };

  static defaultProps = {
    isRunning : false,
    choices : []
  };

  render() {
    const { isRunning, start, stop, reset, addLapTime, choices } = this.props;

    return (
      <div className="Controls">
        { !isRunning ?
            <button
              onClick={ start }
              className="Controls__button"
              ref="startBtn" > Start </button>
          : null }

        { isRunning ?
          <button
            onClick={ stop }
            className="Controls__button"
            ref="stopBtn" > Stop </button>
        : null }

        <button
          onClick={ reset }
          disabled={ isRunning }
          className="Controls__button"
          ref="resetBtn" > Reset </button>

        <button
          onClick={ addLapTime }
          disabled={ !isRunning }
          className="Controls__button"
          ref="lapBtn" > Lap Time </button>

        { isRunning ?
          <div className="obstacleBox">
          {this.props.choices.map((item, index) => {
                return <Row horizontal="center">
                    <div className={item.selected == true ? "obstacleBoxContent activeBox" : "obstacleBoxContent"}>{item.value}</div>
                  </Row>
            })}
          </div>
          : null }
      </div>
    );
  }
}

export default Controls;